.navbarDiv {
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    margin: 0px 100px;
    /* border: 2px solid white; */
    flex-basis: 50%;
    padding-top: 20px;
}

.navbarDiv::before {
    display: none;
    position: absolute;
    content: "";
    width: 100%;
    height: 1px;
    bottom: -15px;
    left: 10%;
    -webkit-transition: 1s ease-in-out;
    transition: 1s ease-in-out;
    -webkit-transform: translate(-10%, -50%);
            transform: translate(-10%, -50%);
    max-width: 100%;
    /* max-width: 0%; */
    background-color: rgb(6, 36, 37);
    box-shadow: 
    0 0 5px 0 rgba(5, 30, 35, 0.3)
    /* , 0 2px 10px 0 rgba(255, 255, 255, 0.4) */
    /* , 0 -2px 10px 0 rgba(255, 255, 255, 0.4) */
    ;
    
}

.navbarItemsList {
    display: flex;
    list-style-type: none;
    padding: 0px;
    margin: 0px;
}

.navbarItemsList li {
    font-size: 25px;
    font-family: Poiret One;
    /* text-transform: uppercase; */
    color: rgb(206, 206, 206);
    -webkit-transition: 0.5s ease-in-out;
    transition: 0.5s ease-in-out;
    cursor: pointer;
    letter-spacing: 0px;
    font-weight: bold;
    opacity: 0;
    -webkit-transform: translateX(-20px);
            transform: translateX(-20px);
}

.navbarItemShow {
    opacity: 1 !important;
    -webkit-transform: unset !important;
            transform: unset !important;
}

.navbarItemsList li:not(:first-child) {
    margin-left: 30px;
}

.navbarItemsList li:hover {
    color: white;
    text-shadow: 
    0 0 5px rgba(255, 255, 255, 0.7)
    , 0 0 30px rgba(255, 255, 255, 0.8)
    /* 0 0 5px rgb(99, 99, 99) */
    /* , 0 1px 5px rgba(255, 255, 255, 0.2) */
    /* , 0 -1px 5px rgba(255, 255, 255, 0.2) */
    ;
}
