.sliderBtn, .sliderBtnReflection {
    cursor: pointer;
    font-family: Allura;
    font-size: 50px;
    font-weight: bold;
    background: none;
    border: none;
    outline: none !important;
    color: white;
    -webkit-transition: 0.5s;
    transition: 0.5s;
    
    /* border: 1px solid cyan; */
    width: 25px;
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 0px;
    
    color: white;
    text-shadow: 
    0 0 5px rgba(255, 255, 255, 0.3)
    , 0 0 20px rgba(255, 255, 255, 0.5)
    ;
    
}

.sliderBtn {
    opacity: 0;
    -webkit-transform: translateY(-20px);
            transform: translateY(-20px);
}

.sliderBtnShow {
    opacity: 1;
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
}

.sliderBtnReflection {
    width: 25px;
    height: 50px;
    margin: 0;
    padding: 0;
    /* border: 1px solid red; */
    color: lightcyan; 
    -webkit-transform: rotateX(180deg) translateY(0px); 
            transform: rotateX(180deg) translateY(0px);
    opacity: 0;
    position: relative;
    z-index: 20;
    margin-top: -55px;
}

.sliderBtnReflectionShow {
    -webkit-transform: rotateX(180deg) translateY(20px);
            transform: rotateX(180deg) translateY(20px);
    opacity: .2;
}


.sliderBtnReflection::after {
    position: absolute;
    content: "";
    background: -webkit-linear-gradient(black 10%, transparent 50%);
    background: linear-gradient(black 10%, transparent 50%);
    width: 100%;
    height: 100%;
}

