    padding: 0;
    height: 100px;
    cursor: pointer;
    margin-top: 70px;
    margin-left: 10px;
    display: flex;
    align-items: flex-start;
    justify-content: center;
    flex-direction: column;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    /* border: 1px solid white; */
}

.sliderBtnAndReflectionDiv:hover .sliderBtn, .sliderBtnAndReflectionDiv:hover .sliderBtnReflection {
    color: white;
    text-shadow: 
    0 0 5px white
    , 0 0 20px white
    ;
}
