.imageSliderItemWrapper {
    margin-left: 10px;
    width: 550px;
    height: 450px;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    /* border: 2px solid red; */
    padding-top: 0;
    overflow: hidden;
    /* filter: grayscale(100%); */
    transition: 0.5s filter;
}

.imageSliderItemWrapper:hover {
    filter: unset;
}

.imageSliderItemWrapperMedium {
    width: 300px;
    height: 350px;
}

.imageSliderItemWrapperSmall {
    width: 250px;
    height: 250px;
}

.imageSliderItem {
    overflow: hidden;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    
    height: 100%;
    width: 100%;
    
    /* border: 4px solid teal; */
    display: flex;
    transition: 1s;
}

.imageSliderItemMedium {
    
}

.imgAndReflectionDiv {
    /* border: 2px solid sandybrown; */
    width: 100%;
    height: 100%;
    
    transition: 0.5s ease-in-out;
    /* transform: translateX(-50%); */
    position: absolute; 
}

.imgAndReflectionDiv:nth-child(1) {
    left: -100%;
}
.imgAndReflectionDiv:nth-child(2) {
    left: 0%;
}
.imgAndReflectionDiv:nth-child(3) {
    left: 100%;
}
.imgAndReflectionDivMedium img {
    width:  auto;
    height: 250px !important;
}

.imgAndReflectionDivSmall img {
    width:  auto;
    height: 200px !important;
}

.imgAndReflectionDivMedium .imgReflection {
    
}

.slideLeft:nth-child(1) {
    /* transform: translateX(-100%) */
    /* transition: 0.5s ease-in-out !important; */
    /* flex-basis: 0%; */
}
.slideLeft:nth-child(2) {
    /* transform: translateX(-100%) */
    /* flex-basis: 100%; */
    /* transition: .3s ease-in-out !important; */
    left: -100%;
}
.slideLeft:nth-child(3) {
    /* transform: translateX(-100%) */
    /* flex-basis: 100%; */
    /* transition: .3s ease-in-out !important; */
    left: 0%;
}

.slideRight:nth-child(1) {
    /* transform: translateX(100%) */
    /* transition: 0.5s ease-in-out !important; */
    /* flex-basis: 0%; */
    left: 0%;
}
.slideRight:nth-child(2) {
    /* transform: translateX(100%) */
    /* flex-basis: 100%; */
    /* transition: .3s ease-in-out !important; */
    left: 100%;    
}

.imageSliderItem:nth-child(2) {
    border: 1px solid white;
}

