.imageSliderItem img {
    width:  auto;
    height: 340px;
    transition: 1s ease-out;
    transform: translateX(0%);
    /* border: 1px solid tomato; */
    position: relative;
    z-index: 5;
    /* opacity: .5; */
    transform: translateY(-50px);
}

.imageSliderItemShow img {
    transform: translateY(-10px);
}

.imageSliderItem::before {
    position: absolute;
    content: "";
    background-color: rgb(0, 0, 0);
    width: 100%;
    height: 100%;
    z-index: 100;
    transition: 1s;
    opacity: 1;
}

.imageSliderItemShow::before {
    opacity: 0;
}

.imageSliderItem img.showImg {
    transform: translateY(-10px);
}

.imageSliderItem img[class~="150"] {
    height: 150px;
    border: 2px solid blue;
}

.imageSliderItem .imgReflection {
    transform: rotateY(0deg) rotateX(180deg) translateY(-40px);
    z-index: 5;
}

.imageSliderItemShow .imgReflection {
    transform: rotateY(0deg) rotateX(180deg);
}

.imageSliderItem .imgReflectionWrapper {
    position: relative;
    /* border: 2px solid white; */
}
.imageSliderItem .imgReflectionWrapper::before {
    position: absolute;
    content: "";
    top: 0px;
    left: 0px;
    width: 100%;
    height: 100%;
    /* background-color: brown; */
    background: linear-gradient(to top, black 75%, transparent 120%);
    z-index: 100;
}

.imageSliderItemWrapper button {
    position: absolute;
    top: 50%;
    right: 10%;
}

.cancelTransition {
    transition: 0s !important;
}