.sliderDiv {
    margin-top: 30px;
    width: 100%;
    min-height: 400px;
    display: flex;
    align-items: flex-start;
    justify-content: center;
    /* border: 2px solid cyan; */
}

/* ABeeZee|Allura|Italiana|Montserrat|Philosopher|Playball|Poiret+One|Quicksand|Sofia */
.sliderBtn, .sliderBtnReflection {
    cursor: pointer;
    font-family: Allura;
    font-size: 50px;
    font-weight: bold;
    background: none;
    border: none;
    outline: none !important;
    color: white;
    transition: 0.5s;
    
    /* border: 1px solid cyan; */
    width: 25px;
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 0px;
    
    color: white;
    text-shadow: 
    0 0 5px rgba(255, 255, 255, 0.3)
    , 0 0 20px rgba(255, 255, 255, 0.5)
    ;
    
}

.sliderBtn {
    opacity: 0;
    transform: translateY(-20px);
}

.sliderBtnShow {
    opacity: 1;
    transform: translateY(0px);
}

.sliderBtnReflection {
    width: 25px;
    height: 50px;
    margin: 0;
    padding: 0;
    /* border: 1px solid red; */
    color: lightcyan; 
    transform: rotateX(180deg) translateY(0px);
    opacity: 0;
    position: relative;
    z-index: 20;
    margin-top: -55px;
}

.sliderBtnReflectionShow {
    transform: rotateX(180deg) translateY(20px);
    opacity: .2;
}


.sliderBtnReflection::after {
    position: absolute;
    content: "";
    background: linear-gradient(black 10%, transparent 50%);
    width: 100%;
    height: 100%;
}

.sliderBtnAndReflectionDiv {
    padding: 0;
    height: 100px;
    cursor: pointer;
    margin-top: 70px;
    margin-left: 10px;
    display: flex;
    align-items: flex-start;
    justify-content: center;
    flex-direction: column;
    user-select: none;
    /* border: 1px solid white; */
}

.sliderBtnAndReflectionDiv:hover .sliderBtn, .sliderBtnAndReflectionDiv:hover .sliderBtnReflection {
    color: white;
    text-shadow: 
    0 0 5px white
    , 0 0 20px white
    ;
}