.logoDiv {
    display: flex;
    align-items: center;
    justify-content: center;
    /* border: 1px solid white; */
    width: 100%;
    position: relative;
    padding-top: 20px;
    padding-bottom: 20px;
    user-select: none;
}

.logoDiv::after{
    /* display: none; */
    /* opacity: 0; */
    position: absolute;
    content: "";
    /* background-color: rgb(62, 62, 62); */
    transition: 1s cubic-bezier(0.445, 0.05, 0.55, 0.95);
    width: 0%;
    /* width: 0%; */
    height: 1px;
    bottom: 10px;
    
    background: linear-gradient(155deg, #42d1ac, #427bd1, #7442d1, #af1331, #af1331, #b3b3b3);
    background-size: 1200% 1200%;
    
    animation: AnimationName 120s ease infinite;

    /* box-shadow: 0 0 20px 1px rgba(255, 255, 255, 0.4); */
}

.logoDiv::before {
    position: absolute;
    content: "";
    /* background-color: rgb(62, 62, 62); */
    transition: 1s cubic-bezier(0.445, 0.05, 0.55, 0.95);
    width: 0%;
    /* width: 0%; */
    height: 2px;
    bottom: 10px;
    
    background: linear-gradient(155deg, #42d1ac, #427bd1, #7442d1, #af1331, #af1331, #b3b3b3);
    background-size: 1200% 1200%;
    
    animation: AnimationName 120s ease infinite;

    /* box-shadow: 0 0 20px 1px rgba(255, 255, 255, 0.4); */

    filter: 
    blur(5px)
    /* brightness(200%) */
    ;
}

.logoDivBar::before, .logoDivBar::after {
    width: 100%;
}

@keyframes AnimationName { 
    0%{background-position:0% 52%}
    50%{background-position:100% 49%}
    100%{background-position:0% 52%}
}

.logoDiv .logoLetterList {
    transition: 0.5s;
    display: flex;
    list-style-type: none;
    padding: 0;
    margin: 0;
    color: white;
    
    font-family: Italiana;
    width: fit-content;
    color: rgb(90, 90, 90);
    /* opacity: 0; */
    cursor: default;
}

.logoDiv .logoLetterList:not(:first-child) {
    /* margin-left: 20px; */
}

.logoDiv .gregLogo {
    font-family: Italiana;
    width: fit-content;
    /* min-width: 100px; */
    /* height: 30px; */
    /* text-transform: ; */
    color: rgb(90, 90, 90);
    text-shadow: 
    0 0 50px rgb(255, 255, 255, 0.5)
    , 0 0 1px rgb(123, 123, 123);
}

.logoDiv .gregLogo li {
    transition: 0.5s;
    /* border: 1px solid red; */
}

.logoDiv .gregLogo li h1 {
    margin: 0;
    padding: 0;
}

.logoDiv .gregLogoDark {
    color: rgb(110, 109, 109);
}

.logoDiv .lewisLogo {
    font-family: Italiana;
    width: fit-content;
    color: rgb(206, 206, 206);
    
    text-shadow: 
    0 0 20px rgb(255, 255, 255, 0.4)
    , 0 0 1px rgb(145, 106, 106);
}

.logoDiv .lewisLogo li {
    transition: 0.5s;
}

.logoDiv .lewisLogo li h1 {
    margin: 0;
    padding: 0;
}



.logoDiv .photographyLogo li {
    transition: 0.5s;
}

.logoLetterLists {
    display: flex;
    align-items: center;
    justify-content: center;
}

.logoDiv .photographyLogo {
    transition: 1.5s all ease-in-out, .5s opacity .8s ease-in-out;
    max-width: 100%;
    overflow: hidden;
    text-shadow: 
    0 0 50px rgb(255, 255, 255, 0.0)
    , 0 0 1px rgb(145, 106, 106);
}

.logoDiv .photographyLogoHide {
    max-width: 0%;
    opacity: 0;
}

.logoLetterLists:hover .photographyLogoHide {
    transition: 1s all ease-in-out, .5s opacity 0s ease-in-out;
    max-width: 100%;
    opacity: 1;
}


.blueprintProductionsImg {
    position: absolute;
    /* border: 1px solid white; */
    margin-right: 50px;
    height: 100px;
    width: auto;
    top: 0px;
    top: 0px;
    left: 0%;
    transition: 0.5s;
    /* transform: scale(2,2); */
    /* background-image: url("../") */
    z-index: 10;
    filter: drop-shadow(0 0 40px rgb(33, 91, 142));
    transform: scale(0,0);
}

.blueprintProductionsImgShow {
    transform: scale(1,1);
}

.blueprintProductionsImg:hover {
    top: 40px;
    transform: scale(2,2);
}