.sliderWrapper {
    display: flex;
    align-items: center;
    justify-content: center;
    
    width: 500px;
    height: 200px;
    border: 2px solid greenyellow;
    overflow: visible;
    position: relative;
}

.sliderImgDiv {
    position: absolute;
    border: 3px solid red;
    width: 100%;
    height: 200px;
}