.showLetter:first-child {
    transition: 0.5s;
    /* color: red; */
    opacity: 1 !important;
    /* transform: transform */
}

.showLetter {
    opacity: 1 !important;
}

.showLetter h1 {
    transition: 0.5s;
    opacity: 1 !important;
    /* color: rebeccapurple; */
    transform: unset !important;
}


/* G first */
.logoDiv .gregLogo li:first-child {
    opacity: 0;
    transform: translateX(135px) translateY(20px);
}

.logoDiv .gregLogo .showLetter:first-child {
    transform: translateX(135px);
    opacity: 1;
}

.logoDiv .gregLogo .moveFirstLeft:first-child {
    transform: translateX(0px);
    opacity: 1;
}

/* L first */
.logoDiv .lewisLogo li:first-child {
    opacity: 0;
    transform: translateX(100px) translateY(-20px);
}

.logoDiv .lewisLogo .showLetter:first-child {
    transform: translateX(100px);
    opacity: 1;
}

.logoDiv .lewisLogo .moveFirstLeft:first-child {
    transform: translateX(0px);
    opacity: 1;
}

/* P first */
.logoDiv .photographyLogo li:first-child {
    transform: translateX(45px)  translateY(20px);
    opacity: 0;
}

.logoDiv .photographyLogo .showLetter:first-child {
    transform: translateX(45px);
    opacity: 1;
}

.logoDiv .photographyLogo .moveFirstLeft:first-child {
    transform: translateX(0px);
    opacity: 1;
}

.logoDiv .gregLogo li:not(:first-child) h1 {
    opacity: 0;
    transform:
     /* translate(-20px) */
     translateY(-40px);
}

.logoDiv .lewisLogo li:not(:first-child) h1 {
    opacity: 0;
    transform:
     /* translate(-20px) */
     translateY(-40px);
}

.logoDiv .photographyLogo li:not(:first-child) h1 {
    opacity: 0;
    transform: 
    /* translate(20px) */
     translateY(40px);
}